import {updateUser} from '@/apis/user'
import {mapState, mapMutations} from 'vuex'
export default {
    name: 'Settings',
    middleware:['auth'],
    data(){
        return {
            currentUser:{
                email:'',
                image: '',
                username:'',
                bio:'',
                password:''
            }
        }
    },
    computed:{
        ...mapState(['user'])
    },
    mounted(){
        Object.assign(this.currentUser,this.user)
    },
    methods:{
        ...mapMutations(['setUser']),
        async onSubmit(){
            try {
                await updateUser(this.currentUser)
                this.setUser(this.currentUser)
            } catch (error) {
                
            }
        }
    }
}
